import { HelperService } from './../../services/helper.service';
import { LoaderService } from 'src/app/shared/loader/loader.service';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BspServiceService } from 'src/app/services/api/bsp-service.service';
import { ItemsModel, ItemModel } from 'src/app/models/item.models';
import { Route } from '@angular/compiler/src/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ParseError } from '@angular/compiler';
import { SuccessDialogModel } from 'src/app/models/dialog.models';

@Component({
  selector: 'app-itemupdate',
  templateUrl: './itemupdate.component.html',
  styleUrls: ['./itemupdate.component.scss'],
})
export class ItemupdateComponent implements OnInit {

  @Input() selectedItem: ItemModel;
  @Output() cancel: EventEmitter<void> = new EventEmitter<void>();
  @Output() update: EventEmitter<void> = new EventEmitter<void>();


  itemId: number;
  item: ItemModel;
  items: ItemsModel;
  itemData: string;
  successData: SuccessDialogModel;

  constructor(
    private bspServiceService: BspServiceService,
    private loaderService: LoaderService,
    private route: ActivatedRoute,
    private router: Router,
    private helperService: HelperService
  ) {
    
    // this.item = new ItemModel();
    // this.itemId = parseInt(this.route.snapshot.paramMap.get("id"));
    // this.item.id = parseInt(this.route.snapshot.paramMap.get("id"));
    // this.item.name = this.route.snapshot.paramMap.get("name");
    // this.item.desc = this.route.snapshot.paramMap.get("desc");
    // this.item.itemPrice = this.route.snapshot.paramMap.get("itemPrice");
    // this.item.itemQuantity = parseInt(this.route.snapshot.paramMap.get("itemQuantity"));
  }

  ngOnInit(): void {
    this.itemId = this.selectedItem.id;
    // console.log('ItemUpdate Component, ' + this.itemData);
    console.log('ngOnInit function called - ' + this.itemId);
  }

  public updateItemPrice() {
    console.log('Update function called');
    const newPrice = parseFloat(
      (document.getElementById('item_price_ip_input') as HTMLInputElement).value
    );
    console.log('Updated Price - ' + newPrice + ' for item_number - ' + this.itemId);
    this.loaderService.show();
    this.bspServiceService.updateItemPrice(this.itemId, newPrice, (result) => {
      if (result) {
        this.update.emit();
        // this.loaderService.dismiss();
        // this.router.navigate(["admin/editData"]);
      } else {
        this.loaderService.dismiss();
        this.helperService.openSomethingWrongDialog();
        alert('Error : Item Price Not Updated');
      }
    });

  }

  public cancelClicked() {
    console.log('Cancel function called');
    // this.router.navigate(['admin/editData']);
    this.cancel.emit();
  }
}
