import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { UserModel } from 'src/app/models/account.models';
import { HelperService } from 'src/app/services/helper.service';
import { StateService } from 'src/app/services/state.service';
import { LoaderService } from 'src/app/shared/loader/loader.service';
import { AccountService } from 'src/app/services/api/account.service';

@Component({
  selector: 'app-user-mgmt',
  templateUrl: './user-mgmt.component.html',
  styleUrls: ['./user-mgmt.component.scss']
})
export class UserMgmtComponent implements OnInit {

  users: UserModel[];
  userTableHeaderColumns: any[];
  searchFields: any[] = ['firstName', 'userEmail'];
  searchHelp: string = "Name OR Email";

  constructor(private loaderService: LoaderService,
              private accountService: AccountService,
              private helperService: HelperService,
              private stateService: StateService,
              private dialog: MatDialog) {
  }

  ngOnInit() {
    this.getRetailerUsers();
    this.buildTableHeaderColumns();
  }

  public onAdd() {
  }

  public onEdit() {
  }

  public onDelete() {
  }

  public getRetailerUsers() {
    this.loaderService.show();
    this.accountService.getUsers().toPromise().then(users => {
      console.log("users :"+JSON.stringify(users));
      this.users = users.sort((u1, u2) => (u1.name > u2.name) ? 1 : -1);
      this.loaderService.dismiss();
    }).catch(error => {
      this.loaderService.dismiss();
      this.helperService.openSomethingWrongDialog(error);
    });
  }

  private buildTableHeaderColumns() {
    this.userTableHeaderColumns = [
      {field: 'firstName', header: 'name'},
      {field: 'userEmail', header: 'email'},
      {field: 'roleId', header: 'role'}
    ];
  }

  private deleteUser() {
  }

}
