import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SiteModel } from 'src/app/models/item.models';
import { BspServiceService } from 'src/app/services/api/bsp-service.service';
import { LoaderService } from 'src/app/shared/loader/loader.service';
import { HelperService } from 'src/app/services/helper.service';
import { MatDialog } from '@angular/material';
import { StateService } from 'src/app/services/state.service';
import {ISites} from './site-details';
var result:any=[{
  "filename": '',
  "result" : ''
}];
@Component({
  selector: 'app-sites',
  templateUrl: './sites.component.html',
  styleUrls: ['./sites.component.scss']
})
export class SitesComponent implements OnInit {

  sites: SiteModel;
  siteName: string ;

  siteId:string;
  OnlineStatus:string = '';


  searchFields: any[] = ['siteName'];
  searchHelp: string = "Site Number";
  searchKeyword: string;
  pageSize = 10;
  pageNumber = 1;
  lastPage = false;
  totalPages = 0;
  totalResult = 0;
  pageContent:any[];
  Sites: ISites[];

  constructor(private route: ActivatedRoute,
    private router: Router,
    private bspServiceService: BspServiceService,
    private loaderService: LoaderService,
    private stateService: StateService,
    private helperService: HelperService,
    private dialog: MatDialog) {
    this.siteId = this.route.snapshot.paramMap.get('id');
  }

  ngOnInit() {
    this.getAllSiteInformation();
   }
  getAllSiteInformation():any{
    this.bspServiceService.getAllSiteInformation().toPromise().then(res => {
      this.lastPage = res.lastPage;
      this.totalPages = res.totalPages;
      this.pageContent = res.pageContent;
      this.Sites = res.pageContent;
      var lenght = this.Sites.length;
      for(let i = 0; i<lenght;i++){
        this.Sites[i].checkStatus = "CheckStatus";
        this.Sites[i].generateFile = "GenerateXML";
        this.Sites[i].ExportFile = "ExportToSite";
      }
     }
   ).catch(error => {
      this.loaderService.dismiss();
      this.helperService.openSomethingWrongDialog(error);
    });
  }
  getStatus(id:string){
    this.siteId = id;
    console.log('ID-' , this.siteId);
    this.loaderService.show();
    this.bspServiceService.getSiteInformation(this.siteId,"PriceBookSiteDetails").toPromise().then(sitesModel => {
      this.sites = sitesModel;
      console.log('ID-' , sitesModel);
      var retstatus = JSON.parse(JSON.stringify(this.sites));
      var lenght = this.Sites.length;
      for(let i=0; i< lenght; i++){
        if(this.Sites[i].id == id){
          this.Sites[i].checkStatus = retstatus.status;
      }
      }

      this.loaderService.dismiss();
     }).catch(error => {
       this.loaderService.dismiss();
       this.helperService.openSomethingWrongDialog(error);
     });
    //return status;
  }

  generateXMLFile(siteName:string):any{

     console.log("Gererate File button called");
     this.bspServiceService.generateXMLForSite(siteName).toPromise().then(res => {
       result["filename"] = res["filename"];
       result["result"] = res["result"];
       var lenght = this.Sites.length;
       for(let i=0; i< lenght; i++){
         if(this.Sites[i].siteName == siteName){
            //this.Sites[i].generateFile = result.result;
            this.Sites[i].generateFile = result.result;
       }
      }
    }).catch(error => {
       this.loaderService.dismiss();
       this.helperService.openSomethingWrongDialog(error);
     });
   }

   GetValueForTheKey(attributes: any, key: string)
   {
     //console.log("in Get attributes" + JSON.stringify(attributes));
     for(let a in attributes)
     {
       var attr = attributes[a];
       //console.log("attr.key" + JSON.stringify(attr));
       if(attr.key == key)
         return attr.value;
     }
     return "Invalid Key";
   }

   exportFile(attributes: any): any {
    var computerName = this.GetValueForTheKey(attributes.attributes, 'computername');
    if(computerName == "Invalid Key")
    {
      this.helperService.openSomethingWrongDialog();
    }
    var dirname = this.GetValueForTheKey(attributes.attributes, 'folder-name');
    if(computerName == "Invalid Key")
    {
      this.helperService.openSomethingWrongDialog();
    }
    console.log("Gererate File button called");
    if(result["result"] == "SUCCESS")
    {
      this.bspServiceService.exportFileToSite(computerName,result.filename, dirname).toPromise().then(res => {
        var lenght = this.Sites.length;
        for(let i=0; i< lenght; i++){
          if(this.Sites[i].customAttributeSets[0].attributes[3].value == computerName){
             this.Sites[i].ExportFile = res.result;
        }
       }
     }
    ).catch(error => {
      this.loaderService.dismiss();
      this.helperService.openSomethingWrongDialog(error);
    });
  }
  else{
    var lenght = this.Sites.length;
    for(let i=0; i< lenght; i++){
      if(this.Sites[i].computerName == computerName){
         //this.Sites[i].exportFile = "FAILED";
         this.Sites[i].ExportFile = "FAILED";
    }
  }
  }
    console.log("Export File button called")
  }


  pageChanged(page: number){
    this.pageNumber = page;

  }
}
