import { Component, OnInit } from '@angular/core';
import { StateService } from 'src/app/services/state.service';
import { Router } from '@angular/router';
import { BspServiceService } from 'src/app/services/api/bsp-service.service';
import { LoaderService } from 'src/app/shared/loader/loader.service';
import { CatsModel, CatModel, ItemsModel , ItemModel} from 'src/app/models/item.models';
import { HelperService } from 'src/app/services/helper.service';

@Component({
  selector: 'app-item-mgmt',
  templateUrl: './item-mgmt.component.html',
  styleUrls: ['./item-mgmt.component.scss']
})
export class ItemMgmtComponent implements OnInit {
  cats: CatsModel;

  selectedCat : CatModel;
  openItemDetails: boolean = false;

  searchFields: any[] = ['name', 'id'];
  searchHelp: string = "Name OR id";
  searchKeyword: string;
  pageSize = 10;
  pageNumber = 1;

  constructor( private bspServiceService: BspServiceService,
    private loaderService: LoaderService,
    private helperService: HelperService,
    private router: Router,
    private stateService: StateService) { }

  ngOnInit(): void {
    this.loaderService.show();
    this.bspServiceService
      .getCats()
      .toPromise()
      .then((cats) => {
        this.cats = cats;
        console.log(cats);
        this.loaderService.dismiss();
      })
      .catch((error) => {
        this.loaderService.dismiss();
        this.helperService.openSomethingWrongDialog(error);
      });
  }

  public ListitemsForCat(cat: CatModel) {
    console.log('Navigate to List items page');
    console.log('Get item details for the Cat' , cat);
    this.router.navigate(['admin/itemDetails', cat.id]);
    //thi.selectedCat = cat;
    //this.openItemDetails = true;
  }

  pageChanged(page: number) {
    this.pageNumber = page;
  }

}
